        title   : 'GitHub Copilot',
        resume  : "GitHub Copilot est généralement disponible pour tous les développeurs Nous mettons GitHub Copilot, un programmeur de paires d'IA qui suggère du code dans votre éditeur, généralement disponible pour tous les développeurs pour 10 USD/mois ou 100 USD/an. Il sera également gratuit pour les étudiants vérifiés et les mainteneurs de projets open source populaires.Chez GitHub, cela fait partie de notre mission de créer une technologie qui rend les développeurs heureux. Il est devenu très clair que l'IA est l'un des meilleurs outils pour autonomiser la prochaine génération de développeurs. Déjà, l'IA fait office de copilote dans notre quotidien. Il nous aide à rédiger des e-mails et des essais, à générer automatiquement des albums photo de nos proches et agit même comme un assistant numérique pour nous aider à commander des courses. Mais jusqu'à présent, l'IA s'est abstenue d'améliorer le code, laissant le processus de développement de logiciels presque entièrement manuel. Cela change maintenant. Aujourd'hui, je suis ravi d'annoncer que nous rendons GitHub Copilot généralement disponible pour les développeurs individuels. Avec GitHub Copilot, pour la première fois dans l'histoire des logiciels, l'IA peut être largement exploitée par les développeurs pour écrire et compléter le code.",
