        resume  : 'React 18 est désormais disponible sur npm ! Dans notre dernier article, nous avons partagé des instructions étape par étape pour mettre à niveau votre application vers React 18 . Dans cet article, nous donnerons un aperçu des nouveautés de React 18 et de ce que cela signifie pour l\'avenir. Notre dernière version majeure comprend des améliorations prêtes à l\'emploi telles que le traitement par lots automatique, de nouvelles API telles que startTransition et le rendu côté serveur en streaming avec prise en charge de Suspense. De nombreuses fonctionnalités de React 18 sont construites sur notre nouveau moteur de rendu simultané, un changement en coulisse qui débloque de nouvelles fonctionnalités puissantes. Concurrent React est opt-in - il n\'est activé que lorsque vous utilisez une fonctionnalité simultanée - mais nous pensons que cela aura un impact important sur la façon dont les gens créent des applications. Nous avons passé des années à rechercher et à développer la prise en charge de la simultanéité dans React, et nous avons pris un soin particulier à fournir une voie d\'adoption progressive aux utilisateurs existants. L\'été dernier, nous avons formé le groupe de travail React 18 pour recueillir les commentaires des experts de la communauté et assurer une expérience de mise à niveau fluide pour l\'ensemble de l\'écosystème React.',
